/* ---------------------------------------------------------
	 GDS BUTTONS — ODBWAIL Design Language
---------------------------------------------------------- */

.gds-btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding: var(--space-3) var(--space-5);
	border-radius: var(--radius-md);
	font-family: Inter, sans-serif;
	font-size: var(--font-size-md);
	font-weight: var(--font-weight-medium);
	cursor: pointer;
	transition: all var(--duration-fast) var(--ease-default);
	border: none;
	outline: none;
}

/* Primary */
.gds-btn-primary {
	background: var(--color-brand-1);
	color: white;
}

.gds-btn-primary:hover {
	background: var(--color-brand-2);
}

.gds-btn-primary:active {
	background: var(--color-brand-3);
}

/* Secondary */
.gds-btn-secondary {
	background: var(--color-neutral-100);
	color: var(--color-neutral-800);
	border: 1px solid var(--color-neutral-300);
}

.gds-btn-secondary:hover {
	background: var(--color-neutral-200);
}

/* Ghost */
.gds-btn-ghost {
	background: transparent;
	color: var(--color-neutral-700);
}

.gds-btn-ghost:hover {
	background: var(--color-neutral-100);
}

/* Destructive */
.gds-btn-destructive {
	background: #dc2626;
	color: white;
}

.gds-btn-destructive:hover {
	background: #b91c1c;
}

/* Disabled */
.gds-btn:disabled {
	opacity: 0.5;
	cursor: not-allowed;
}

/* Loading */
.gds-btn-loading {
	position: relative;
	pointer-events: none;
}

.gds-btn-loading::after {
	content: "";
	width: 16px;
	height: 16px;
	border: 2px solid white;
	border-top-color: transparent;
	border-radius: 50%;
	animation: spin var(--duration-slow) linear infinite;
	margin-left: var(--space-2);
}

@keyframes spin {
	to {
		transform: rotate(360deg);
	}
}

/* ---------------------------------------------------------
	 GDS INPUTS
---------------------------------------------------------- */

.gds-input-wrapper {
	display: flex;
	flex-direction: column;
	gap: var(--space-2);
}

.gds-input-label {
	font-size: var(--font-size-sm);
	color: var(--color-neutral-700);
	font-weight: var(--font-weight-medium);
}

.gds-input {
	padding: var(--space-3);
	border-radius: var(--radius-md);
	border: 1px solid var(--color-neutral-300);
	background: white;
	font-size: var(--font-size-md);
	transition: border var(--duration-fast) var(--ease-default),
							box-shadow var(--duration-fast) var(--ease-default);
}

.gds-input:focus {
	border-color: var(--color-brand-1);
	box-shadow: 0 0 0 3px rgba(0, 120, 255, 0.2);
	outline: none;
}

.gds-input-error {
	border-color: #dc2626;
}

.gds-input-helper {
	font-size: var(--font-size-xs);
	color: var(--color-neutral-600);
}

.gds-input-error-text {
	color: #dc2626;
}

/* ---------------------------------------------------------
	 GDS CARDS
---------------------------------------------------------- */

.gds-card {
	background: white;
	border-radius: var(--radius-lg);
	padding: var(--space-6);
	box-shadow: var(--shadow-md);
	transition: transform var(--duration-fast) var(--ease-default),
							box-shadow var(--duration-fast) var(--ease-default);
}

.gds-card:hover {
	transform: translateY(-2px);
	box-shadow: var(--shadow-lg);
}

.gds-card-title {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-xl);
	margin-bottom: var(--space-3);
}

.gds-card-body {
	font-size: var(--font-size-md);
	color: var(--color-neutral-700);
}

/* DOCS LAYOUT + NAV helpers (mirrors docs.css but kept here for GDS cohesion) */
.docs-content {
	flex: 1;
	max-width: 760px;
}

/* ---------------------------------------------------------
	 GDS MODALS
---------------------------------------------------------- */

.gds-modal-overlay {
	position: fixed;
	inset: 0;
	background: rgba(0,0,0,0.55);
	backdrop-filter: blur(4px);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 50;
}

.gds-modal {
	background: white;
	border-radius: var(--radius-lg);
	padding: var(--space-8);
	width: 90%;
	max-width: 520px;
	box-shadow: var(--shadow-xl);
	animation: modalIn var(--duration-medium) var(--ease-default);
}

@keyframes modalIn {
	from { opacity: 0; transform: translateY(20px); }
	to { opacity: 1; transform: translateY(0); }
}

.gds-modal-header {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-2xl);
	margin-bottom: var(--space-4);
}

.gds-modal-body {
	font-size: var(--font-size-md);
	color: var(--color-neutral-700);
	margin-bottom: var(--space-6);
}

.gds-modal-actions {
	display: flex;
	justify-content: flex-end;
	gap: var(--space-3);
}

/* ---------------------------------------------------------
	 GDS TABLES
---------------------------------------------------------- */

.gds-table {
	width: 100%;
	border-collapse: collapse;
	margin-bottom: var(--space-8);
}

.gds-table th {
	background: var(--color-neutral-100);
	padding: var(--space-3);
	text-align: left;
	font-weight: var(--font-weight-semibold);
	border-bottom: 1px solid var(--color-neutral-300);
}

.gds-table td {
	padding: var(--space-3);
	border-bottom: 1px solid var(--color-neutral-200);
	color: var(--color-neutral-700);
}

.gds-table-row:hover {
	background: var(--color-neutral-50);
}

/* ---------------------------------------------------------
	 GDS TAGS / BADGES
---------------------------------------------------------- */

.gds-tag {
	display: inline-block;
	padding: 4px 10px;
	border-radius: var(--radius-full);
	font-size: var(--font-size-sm);
	font-weight: var(--font-weight-medium);
	color: white;
}

.gds-tag-available { background: #16a34a; }
.gds-tag-taken { background: #dc2626; }
.gds-tag-premium { background: #ca8a04; }
.gds-tag-info { background: var(--color-brand-1); }
.gds-tag-neutral { background: var(--color-neutral-500); }

/* ---------------------------------------------------------
	 GDS HEADER + FOOTER
---------------------------------------------------------- */

.gds-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: var(--space-4) var(--space-8);
	background: white;
	border-bottom: 1px solid var(--color-neutral-200);
}

.gds-header-logo {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-xl);
	color: var(--color-brand-1);
}

.gds-header-nav {
	display: flex;
	gap: var(--space-6);
}

.gds-header-link {
	font-size: var(--font-size-md);
	color: var(--color-neutral-700);
	text-decoration: none;
	transition: color var(--duration-fast) var(--ease-default);
}

.gds-header-link:hover {
	color: var(--color-brand-1);
}

.gds-footer {
	padding: var(--space-8);
	background: var(--color-neutral-100);
	text-align: center;
	color: var(--color-neutral-600);
	font-size: var(--font-size-sm);
}

/* ---------------------------------------------------------
	 BUILDER LAYOUT
---------------------------------------------------------- */

.builder-landing {
	padding: var(--space-10);
	text-align: center;
}

.builder-landing .hero h1 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-5xl);
	margin-bottom: var(--space-4);
}

.builder-landing .hero p {
	font-size: var(--font-size-lg);
	color: var(--color-neutral-600);
	margin-bottom: var(--space-6);
}

.builder-landing .features {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
	gap: var(--space-6);
	margin-top: var(--space-10);
}

.builder-start {
	max-width: 640px;
	margin: 0 auto;
	padding: var(--space-10);
}

.builder-start h1 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-3xl);
	margin-bottom: var(--space-6);
}

.builder-domain {
	max-width: 720px;
	margin: 0 auto;
	padding: var(--space-10);
}

.domain-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: var(--space-4);
	border: 1px solid var(--color-neutral-200);
	border-radius: var(--radius-md);
	margin-bottom: var(--space-4);
}

.builder-canvas {
	display: flex;
	height: calc(100vh - 80px);
}

.builder-sidebar {
	width: 280px;
	border-right: 1px solid var(--color-neutral-200);
	padding: var(--space-6);
	overflow-y: auto;
}

.builder-preview {
	flex: 1;
	background: var(--color-neutral-50);
	padding: var(--space-6);
}

.builder-iframe {
	width: 100%;
	height: 100%;
	border: none;
	border-radius: var(--radius-lg);
	box-shadow: var(--shadow-lg);
}

/* ---------------------------------------------------------
	 DOMAIN LIFECYCLE UI
---------------------------------------------------------- */

.domain-search {
	max-width: 720px;
	margin: 0 auto;
	padding: var(--space-10);
}

.domain-search h1 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-3xl);
	margin-bottom: var(--space-6);
}

.domain-details {
	max-width: 800px;
	margin: 0 auto;
	padding: var(--space-10);
}

.domain-details h1 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-4xl);
	margin-bottom: var(--space-6);
}

.domain-details h2 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-2xl);
	margin-top: var(--space-8);
	margin-bottom: var(--space-4);
}

/* ---------------------------------------------------------
	 AI GENERATION MODAL
---------------------------------------------------------- */

.ai-modal-body {
	text-align: center;
	padding: var(--space-6);
}

.ai-spinner {
	width: 48px;
	height: 48px;
	border: 4px solid var(--color-neutral-200);
	border-top-color: var(--color-brand-1);
	border-radius: 50%;
	margin: 0 auto var(--space-4);
	animation: spin var(--duration-slow) linear infinite;
}

.ai-tips {
	margin-top: var(--space-4);
	color: var(--color-neutral-600);
	font-size: var(--font-size-sm);
}

/* ---------------------------------------------------------
	 BUILDER SIDEBAR
---------------------------------------------------------- */

.builder-sidebar h3 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-lg);
	margin-bottom: var(--space-3);
}

.builder-sidebar ul {
	list-style: none;
	padding-left: 0;
	margin-bottom: var(--space-6);
}

.builder-sidebar li {
	padding: var(--space-2) 0;
	cursor: pointer;
	color: var(--color-neutral-700);
	transition: color var(--duration-fast) var(--ease-default);
}

.builder-sidebar li:hover {
	color: var(--color-brand-1);
}

/* ---------------------------------------------------------
	 RDAP DETAIL PANEL
---------------------------------------------------------- */

.rdap-panel {
	background: white;
	border-radius: var(--radius-lg);
	padding: var(--space-6);
	box-shadow: var(--shadow-md);
	margin-top: var(--space-6);
}

.rdap-row {
	display: flex;
	justify-content: space-between;
	padding: var(--space-3) 0;
	border-bottom: 1px solid var(--color-neutral-200);
}

.rdap-row:last-child {
	border-bottom: none;
}

.rdap-label {
	font-weight: var(--font-weight-medium);
	color: var(--color-neutral-700);
}

.rdap-value {
	color: var(--color-neutral-600);
}

/* ---------------------------------------------------------
	 DNS VIEWER
---------------------------------------------------------- */
.dns-viewer {
	margin-top: var(--space-8);
	background: white;
	padding: var(--space-6);
	border-radius: var(--radius-lg);
	box-shadow: var(--shadow-md);
}

.dns-viewer h3 {
	font-family: var(--font-family-headings);
	font-size: var(--font-size-xl);
	margin-bottom: var(--space-4);
}

/* ---------------------------------------------------------
	 DOMAIN SUGGESTION CARD
---------------------------------------------------------- */
.domain-suggestion-card {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: var(--space-4);
	border: 1px solid var(--color-neutral-200);
	border-radius: var(--radius-md);
	margin-bottom: var(--space-4);
	background: white;
	transition: box-shadow var(--duration-fast) var(--ease-default);
}

.domain-suggestion-card:hover {
	box-shadow: var(--shadow-md);
}

/* ---------------------------------------------------------
	 BUILDER TOOLBAR
---------------------------------------------------------- */
.builder-toolbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: var(--space-4);
	background: white;
	border-bottom: 1px solid var(--color-neutral-200);
}

.builder-toolbar-left,
.builder-toolbar-right {
	display: flex;
	gap: var(--space-3);
}

/* ---------------------------------------------------------
	 AI SECTION CONTROLS
---------------------------------------------------------- */
.ai-section-controls {
	display: flex;
	gap: var(--space-3);
	margin-top: var(--space-4);
}

/* ---------------------------------------------------------
	 BRANDING CONTROLS
---------------------------------------------------------- */
.branding-controls {
	margin-top: var(--space-6);
}

.branding-controls h3 {
	font-family: var(--font-family-headings);
	margin-bottom: var(--space-3);
}

.branding-option {
	margin-bottom: var(--space-4);
}

/* ---------------------------------------------------------
	 DOMAIN CONNECT HELPER
---------------------------------------------------------- */
.domain-connect {
	max-width: 720px;
	margin: 0 auto;
	padding: var(--space-10);
}

.domain-connect-step {
	margin-bottom: var(--space-8);
}

.domain-connect-step h3 {
	font-family: var(--font-family-headings);
	margin-bottom: var(--space-3);
}


